:root {
    --bg-button: white;
    --border-button: solid 1px gray;
}

button {
    font-size: 1.4em;
    background-color: var(--bg-button);
    border: 1px solid rgba(128, 128, 128, 0.514);
    outline: none;
}

button:active{
    background-color: #ccc;
}


.button.double{
    grid-column: span 2;
}

.button.triple{
    grid-column: span 3;
}

.button.operation{
    background-color: #fa8231;
    color: white;
    border: 1px solid #a35520;

}

.button.operation:active {
    background-color: #fa8231cc;

}